import { Component, h, Method, State, Listen, Prop } from "@stencil/core";

@Component({
    tag: "wxcc-auth-demo-widget",
    styleUrl: "wxcc-auth-demo-widget.css",
    shadow: true
})

export class wxccAuth {

    @State() token
    @State() expStamp
    @State() hide = true
    @Prop() callbackUrl: string
    @State() deets
    @State() request
    @State() showResponse = false

    @Method()
    async burp(x) {
        console.log(x)
        const code = x.slice(x.search("=") + 1, x.search("&"))
        // console.log(code)
        let myHeaders = new Headers();
        myHeaders.append("Content-Type", "application/json");
        // let urlencoded = new URLSearchParams();
        // urlencoded.append("grant_type", "authorization_code")
        // urlencoded.append("redirect_uri", this.callbackUrl)
        // urlencoded.append("client_id", "C1b249680a1788a109f1cd53c578c874a28a3e6468929598d4552d17d0a12f32f")
        // urlencoded.append("client_secret", "49aad1cd3ed0758755c69d818baf897923876854423dbc01d608fc8255d14044")
        // urlencoded.append("code", code)
        let urlencoded = { "grant_type": "authorization_code", "redirect_uri": this.callbackUrl, "client_id": "C1b249680a1788a109f1cd53c578c874a28a3e6468929598d4552d17d0a12f32f", "client_secret": "49aad1cd3ed0758755c69d818baf897923876854423dbc01d608fc8255d14044", "code": code }
        let requestOptions = {
            method: 'POST',
            headers: myHeaders,
            body: JSON.stringify(urlencoded)
        };
        // console.log(urlencoded.toString())
        this.request = urlencoded //.toString() //.replace(/["&"]/g,"\r\n")
        let response = await fetch("https://webexapis.com/v1/access_token", requestOptions)
        this.deets = await response.json()
        // console.log(await this.deets)
        this.token = "Bearer " + this.deets.access_token
        this.expStamp = new Date(Date.now() + (this.deets.expires_in * 1000))
        // sessionStorage.setItem("token", this.token)
        // sessionStorage.setItem("expStamp", this.expStamp)
        // console.log(this.token)
        // console.log(this.expStamp)

        this.hide = false
        this.showResponse = false


    }
    @Listen('wxccAuthPop', { target: "body" })
    onGetAuth() {
        if (+new Date(sessionStorage.getItem("expStamp")) > Date.now()) {
            this.token = sessionStorage.getItem("token")
            this.hide = false
            this.showResponse = false
        }
        else {
            window.open(`https://webexapis.com/v1/authorize?response_type=code&client_id=C1b249680a1788a109f1cd53c578c874a28a3e6468929598d4552d17d0a12f32f&state=new&scope=cjp%3Aconfig%20cjp%3Aconfig_read%20cjp%3Aconfig_write&redirect_uri=${this.callbackUrl}`, "steve", "popup")
        }

    }

    render() {

        let tok
        if(this.request){
        tok = <p><h1>The code is sent with this information from the app </h1>
                grant_type: {this.request.grant_type}<br></br>
                redirect_uri: {this.request.redirect_uri}<br></br>
                client_id: {this.request.client_id}<br></br>
                client_secret: {this.request.client_secret}<br></br>
                <strong>code:{this.request.code}</strong><br></br>
            </p>
        if (this.showResponse) {
            tok = <p><h1>This is the response with the token information </h1>
                    access_token: {this.deets.access_token}<br></br>
                    expires_in: {this.deets.expires_in}<br></br>
                    refresh_token: {this.deets.refresh_token}<br></br>
                    refresh_token_expires_in: {this.deets.refresh_token_expires_in}<br></br>
                    token_type: {this.deets.token_type}<br></br>
                    scope: {this.deets.scope}

                </p>
        }

    }
        // deets: ${JSON.stringify(this.deets)}

        // {
        //     "headers":{
        //     "Authorization":"${this.token}"
        //     }
        // }`

        return [
            <div class={"frame" + (this.hide ? " hidden" : "")}>
                {/* <h1>Copy this text into your Global Environment Variable</h1> */}
                <p>
                    {tok}
                </p>
                <button onClick={() => {this.showResponse = !this.showResponse}}>{this.showResponse ? "Show Request" : "Show Response"}</button><button onClick={() => { this.hide = !this.hide }}>close</button>
                {/* <button onClick={this.onGetAuth.bind(this)}>{this.expStamp>Date.now() ? `Auth good until ${this.expStamp}`:"Launch Auth"}</button> */}
            </div>
        ]
    }
}